	module.exports = {"data":{"markdownRemark":{"html":"<h2>Trojans</h2>\n<p>Trojans are programs or software that actually appear to be legitimate but in reality are malicious or harmful.</p>\n<p>An example is a program you download from the internet which claims to be an image editor but in reality, whenever you open it, malware is spread on your computer.</p>\n<p>They can go unnoticed for a long time doing the following things meanwhile:</p>\n<ul>\n \t<li>Spying on you by sending your information to another computer.</li>\n \t<li>Using your computer for malicious purposes such as contributing to a Distributed Denial of Service(DDoS) attack.</li>\n \t<li>Creating further loopholes in your security so other malware can also infect your computer.</li>\n \t<li>Using up valuable disk space and making your computer slow.</li>\n</ul>\nIn order to remain safe from Trojans, you should always download programs from reputable websites and also enable an anti-virus program like Avast or AVG on your computer.\n<p> </p>\n<h4>More Information:</h4>\n<p><a href='https://en.wikipedia.org/wiki/Trojan_horse_(computing)' target='_blank' rel='nofollow'>Trojan Horses on Wikipedia</a>.\n<a href=\"https://www.avg.com/en/signal/what-is-a-trojan\">Trojan Horse Fundamentals - AVG</a></p>","fields":{"slug":"/security/trojans/"},"frontmatter":{"title":"Trojans"}}},"pathContext":{"slug":"/security/trojans/"}}

